<template>
  <div class="d3box">
    <svg id="a2"></svg>
    
  </div>
</template>
<script>
import dat from "@/assets/dat.js";
export default {
  name: "demo",
  data() {
    return {
      aa: dat.data,
      setval: null,
    };
  },
  mounted() {
    console.log('%cdata--length', 'color:green',this.aa.length)
    this.d3draw(this.getArrRandomly(this.aa));
    this.setval = setInterval(() => {
      this.d3draw(this.getArrRandomly(this.aa));
    }, 1000);
  },
  beforeDestroy() {
    window.clearInterval(this.setval);
    this.setval = null;
  },
  methods: {
    getArrRandomly(arr) {
      var len = arr.length;
      //首先从最大的数开始遍历，之后递减
      for (var i = len - 1; i >= 0; i--) {
        //随机索引值randomIndex是从0-arr.length中随机抽取的
        var randomIndex = Math.floor(Math.random() * (i + 1));
        //下面三句相当于把从数组中随机抽取到的值与当前遍历的值互换位置
        var itemIndex = arr[randomIndex];
        arr[randomIndex] = arr[i];
        arr[i] = itemIndex;
      }
      //每一次的遍历都相当于把从数组中随机抽取（不重复）的一个元素放到数组的最后面（索引顺序为：len-1,len-2,len-3......0）
      return arr;
    },
    d3draw(val) {
      if (!val) return;
      var height = 450,
        width = height / 1.138,
        top_margin = ~~(width / 13);

      var svg = d3
        .select("#a2")
        svg.selectAll("g").remove(); //这里就是用来清空原先绘图的元素的
        svg.attr("width", width)
        .attr("height", height)
        .append("g")
        .attr("transform", `translate(${0},${0})`);

      var data = val;
      const x = d3.scale
        .linear()
        .domain([0, data[0].length - 1])
        .range([0, width]);
      const y = d3.scale
        .linear()
        .domain([0, data.length - 1])
        .range([top_margin, height]);

      var line = d3.svg
        .line()
        .x((d, i) => x(i))
        .y((d) => -d * 0.8);
      /* 画主图部分开始 */

      svg
        .append("g")
        .selectAll(".wave")
        .data(data)
        .enter()
        .append("path")
        .attr("transform", (d, i) => `translate(0,${y(i * 0.6)})`)
        .attr("class", "wave")
        .attr("d", line);
      /* 画主图部分结束 */
    },
  },
};
</script>
<style lang="scss">
 svg {
    /*   background-color: rgb(230, 224, 224); */
    .wave {
      fill: white;
      stroke: #444;
      stroke-width: 1;
    }
    // .wave:hover {
    //   fill: rgb(207, 38, 38);
    //   stroke: rgb(207, 38, 38);
    //   stroke-width: 1;
    // }
  }
</style>



