<template>
  <div class="hello">
    <div class="outbox">
      <div class="box1">
        <demo/>
      </div>
      <div class="box2">
      <demo2/>
      </div>
    </div>
  </div>
</template>

<script>
import demo from './demo'
import demo2 from './demo2'

export default {
  name: "HelloWorld",
  components:{
    demo,
    demo2
  
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.hello {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  width: 100vw;
  height: 100vh;
  /* background-color: aquamarine; */
  .outbox {
    display: flex;
    flex-flow:row nowrap;
    justify-content: space-around;
    align-items: center;
    width: 100vw;
    height:100vh;
  /*   background-color: aquamarine; */
    .box1{
       display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    width: 40vw;
    height: 60vh;
    margin-right: 30px;
    //background-color: aquamarine;
    }
    .box2{
       display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    width: 40vw;
    height: 60vh;
   /*  background-color: aquamarine; */
    }
  }
}
</style>



